import Vue from 'vue'
import Router from 'vue-router'

import ContactDetails from '@/components/ContactDetails.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/ContactDetails／:id',
      name: 'ContactDetails',
      component: ContactDetails
    }
  ]
})



