<template>
  <div class="contact-details">
    <div class="h3">
        contact details
    </div>
   <div>
        <p>Name: {{msg}}</p>
        <p>address: ABC str, NSW</p>
        <p>address: ABC str, NSW</p>
        <p>address: ABC str, NSW</p>
        <p>address: ABC str, NSW</p>
   </div>
  </div>
</template>

<script>
export default {
  name: 'ContactDetails',
  data () {
      return {
        msg: ''
      }
  },
  methods: {
    getParams () {
      // 取到路由带过来的参数 
      let routerParams = this.$route.params.id
      // 将数据放在当前组件的数据内
      this.msg = routerParams
    }
  },
  watch: {
  // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    '$route': 'getParams'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.contact-details {
    font-size:14px;
}
</style>



